<template>
    <div class="condition">
        <input type="checkbox" class="checkbox" v-model="checked" @click="conditionChecked()">
        <label>{{ condition.name }}</label>
    </div>
</template>

<script>


export default {
    name: "Condition",
    data () {
        return {
            checked: false,
        }
    },
    methods: {
        conditionChecked() {
            if (!this.checked) {
                this.$root.$refs.conditionList.addCondition(this.condition);
            } else {
                this.$root.$refs.conditionList.removeCondition(this.condition);
            }
        }
    },
    props :{
        condition : Object,
    },
    created () {
        this.checked = this.condition.state;
    },
    
};
</script>

<style scoped>
.condition {
    display: flex;
}
label {
    font-size: 0.9em;
    margin-left: 10px;
}

</style>
