<template>
    <div class="conditionContainer">
        <h3>Conditions de travail : </h3>
        <ul v-for="condition in conditions" v-bind:key="condition.id">
            <precondition-item :condition='condition' v-if="condition.id > 0 && condition.id <= 2"></precondition-item>
            <condition-item :condition='condition' v-if="condition.id > 2"></condition-item>
        </ul>
    </div>
</template>

<script>
import conditions from './../..//utility/conditions.JSON';
import preconditionItem from './precondition-item.vue';
import ConditionItem from './condition-item.vue';

export default {
    data() {
        return {
            conditions: conditions.conditions,
            selectedConditions: [],
            selectedPreconditions: [],
        }
    },
    components: {
        preconditionItem,
        ConditionItem
    },
    methods : {
        addPrecondition(condition) {
            this.conditions[condition.id].state = true;
            this.selectedConditions.push(condition.id);
            this.$root.$refs.domainList.updateConditions(this.selectedConditions);
        },
        removePrecondition(condition) {
            this.conditions[condition.id].state = false;
            this.selectedConditions = this.selectedConditions.filter(data => data != condition.id);
            this.$root.$refs.domainList.updateConditions(this.selectedConditions);
        },
        addCondition(condition) {
            this.conditions[condition.id].state = true;
            this.selectedConditions.push(condition.id);
            this.$root.$refs.domainList.updateConditions(this.selectedConditions);
        },
        removeCondition(condition) {
            this.conditions[condition.id].state = false;
            this.selectedConditions = this.selectedConditions.filter(data => data != condition.id);
            this.$root.$refs.domainList.updateConditions(this.selectedConditions);
        }
    },
    created () {
        this.$root.$refs.conditionList = this;
    },
}
</script>

<style scoped>
    h3 {
        text-align: center;
        padding: 15px 0px;
        margin-top: 40px;
    }
</style>
