            <h1>Plan de carrière DRI</h1>
            <a class="a1">L’outil suivant est mis à votre disposition vous permettre de mieux connaitre les options de carrière au sein de la direction.</a>
            <a class="a2">Vos choix de la section vont raffiner les possibilités qui vous seront présentés!</a>
          </div>
          <div class="contentCard" v-if="page == 1">
            <h1>Première étape:</h1>
            <a class="a1"> Cocher uniquement les énoncés qui sont vrais puisque ceux-ci vont filtrer les possibilités qui vous seront présentés. À titre d’exemple, si vous sélectionnez «je souhaite faire du télétravail à 100%», aucun emploi requérant des tâches en présentiel ne vous sera présenté.</a>
            <condition-list/>
          </div>
          <div v-if="page == 2">
            <h1>Deuxième étape:</h1>
            <a class="a1"> Cocher toutes les sphères qui vous intéressent. Les intérêts sont souvent partagés dans plusieurs secteurs de la DRI.</a>
            <interest-list></interest-list>
          </div>
          <div v-if="page == 3">
              <h1>Troisième étape:</h1>
              <div><a class="a1">Cette section n’influence pas les résultats, elle permet simplement de les afficher selon votre préférence.</a>
                <sorting-list></sorting-list>
                <a class="a1">Afin de vous permettre de bien connaitre les choix de carrière à la DRI, les emplois exclus par votre sélection vont s’afficher au bas de la page.</a>
                <a class="a1">N’hésitez pas à moduler vos filtres pour valider leur impact sur les perspectives de la direction.</a>
              </div>
          </div>
          <div class="buttons">
            <button @click="previousPage()" v-if="page != 0">Précédent</button>
            <button @click="nextPage()" v-if="page != 3">Suivant</button>
            <button v-if="page == 3" @click="applyFilters()">Appliquer</button>
          </div>
        </div>
    </div>
</template>

<script>
import ConditionList from './../Filters/condition-list.vue'
import InterestList from './../Filters/interest-list.vue'
import SortingList from './../Filters/sorting-list.vue'

export default {
  data() {
    return {
      page: 0,
    }
  },
  components: {
    ConditionList,
    InterestList,
    SortingList
  },
  methods: {
    nextPage() {
      this.page++;
    },
    previousPage() {
      this.page--;
    },
    applyFilters() {
      this.page = -1;
    },
  }
};
</script>

