<template>
    <div>
        <ul v-if="filterOption == 'domain'">
            <transition-group name="slide-down">
                <li v-for="domain in filteredDomains" v-bind:key="domain.id">
                    <domain-item :domain="domain" v-if="domain.jobs.length != 0"/>
                </li>
            </transition-group>
        </ul>
        <ul v-if="filterOption == 'job'">
            <transition-group name="slide-down">
                <li v-for="job in filteredJobs" v-bind:key="job.id">
                    <job-item :job="job"/>
                    <p>{{job.domain}}</p>
                </li>
            </transition-group>
        </ul>
    </div>
</template>

<script>
    import DomainItem from './domain-item.vue';
    import domains from './../../utility/domains.JSON';
    import jobs from './../../utility/jobs.JSON';
    import jobItem from './job-item.vue';
    import _ from 'lodash';

    export default {
        name: "DomainList",
        data() {
            return {
                domains: domains.domains,
                jobs: jobs.jobs,
                filteredDomains: [],
                filteredJobs: [],
                conditions: [],
                interests: [],
                types:[],
                filterOption: "domain",
            }
        },
        components: {
            DomainItem,
            jobItem
        },
        methods : {
            updateConditions(conditions) {
                this.conditions = conditions;
                this.updateJobs();
            },
            updateInterests(interests) {
                this.interests = interests;
                this.updateJobs();
            },
            updateTypes(types) {
                this.types = types;
                this.updateJobs();
            },
            updateJobs() {
                this.filteredJobs = this.jobs;
                if(this.conditions.length > 0){
                    this.conditionStrictFilter();
                }
                if(this.interests.length > 0){
                    this.interestSuggestiveFilter();
                }
                if(this.types.length > 0){
                    this.typeFilter();
                }
                if(this.filterOption == 'domain'){
                    this.updateDomains();
                }
            },
            updateDomains() {
                this.filteredDomains = this.domains;
                for(let i = 0; i < this.filteredDomains.length; i++){
                    this.filteredDomains[i].jobs = this.filteredJobs.filter(job => job.domain == this.filteredDomains[i].id);
                }
            },
            conditionStrictFilter(){
                this.filteredJobs = this.filteredJobs.filter
                (job => _.intersection(job.conditions, this.conditions).length == this.conditions.length);
            },
            interestStrictFilter(){
                this.filteredJobs = this.filteredJobs.filter
                (job => _.intersection(job.interests, this.interests).length == this.interests.length);
            },
            conditionSuggestiveFilter(){
                this.filteredJobs = this.filteredJobs.filter
                (job => _.intersection(job.conditions, this.conditions).length > 0);
            },
            interestSuggestiveFilter(){
                this.filteredJobs = this.filteredJobs.filter
                (job => _.intersection(job.interests, this.interests).length > 0);
            },
            typeFilter(){
                this.filteredJobs = this.filteredJobs.filter
                (job => _.indexOf(this.types, job.name) > -1);
            }
            
        },
        created() {
            this.$root.$refs.domainList = this;
            this.updateJobs();
        },
    };
</script>

<style scoped>
    .slide-down-enter-active,.slide-down-move{
    transition: all 0.5s;
    }
    .slide-down-enter {
    transform: translateY(-10px);
    opacity: 0;
    }
    h2 {
        padding : 10px;
    }
    li {
        list-style: none;
    }
</style>