<template>
    <div class="sorting">
        <input type="checkbox" class="checkbox" v-model="checked" @click="sortChecked()">
        <label>{{ type.name }}</label>
    </div>
</template>

<script>


export default {
    name: "Sortihng",
    data() {
        return {
            checked: false,
        }
    },
    methods: {
        sortChecked() {
            if (!this.checked) {
                this.$root.$refs.sortingList.addType(this.type);
            } else {
                this.$root.$refs.sortingList.removeType(this.type);
            }
        }
    },
    props :{
        type : Object,
    },
    created () {
        this.checked = this.type.state;
    },
};
</script>

<style scoped>
.sorting {
    display: flex;
    align-items: center;
    justify-content: flex-start;
}
label {
    font-size: 0.9em;
    margin-left: 10px;
}

</style>
