<template>
    <div>
        <button @click="showFilters()">Filtres{{showIcon}}</button>
        <aside class="menu" v-if="showMenu == true">
            <search-bar v-if="showSearchBar"></search-bar>
            <condition-list></condition-list>
            <interest-list></interest-list>
            <sorting-list></sorting-list>
        </aside>
    </div>
</template>

<script>
    import ConditionList from './condition-list.vue';
    import InterestList from './interest-list.vue';
    import SearchBar from './search-bar.vue';
    import SortingList from './sorting-list.vue';

    export default {
        name: "header-content",
        data () {
            return {
                showSearchBar: false,
                showMenu: false,
                showIcon: "+",
            }
        },
        components: {
            InterestList,
            SearchBar,
            ConditionList,
            SortingList
        },
        methods: {
            showFilters() {
                this.showMenu = !this.showMenu;
                if (this.showMenu) {
                    this.showIcon = "-";
                } else {
                    this.showIcon = "+";
                }
            }
        }
    };
</script>

<style scoped>
    .menu {
        display: flex;
        flex-direction: column;
        padding: 10px;
        background-color: var(--tertiary);
        width : 100%;
        top: 0;
    }
    button {
        width: 115px;
        height: 40px;
        border-radius: 8px;
        border: 1px solid black;
        font-size: 1.2em;
        font-weight: bold;
        padding: 10px;
        cursor: pointer;
        border: none;
        transition: all 0.2s;
        margin: 10px 0px 0px;
    }
    button:hover {
        box-shadow: 0 8px 16px 0 rgba(0,0,0,0.5);
        border-radius: 4px;
    }
</style>